.cards {
    display: flex;
    flex-wrap: wrap;
    gap: 32px;
}

.contacts-list {
    background: #FFFFFF;
    padding: 40px;
    margin-right: auto;
    border: 1px solid #E1E1E1;
    margin-bottom: 20px;
    width: 368px;
    height: 538px;
    border-radius: 24px;
   
}

.contacts__title {
    width: 75px;
    height: 28px;
    background: var(--accent-color);
    border-radius: 6px;
    text-align: center;
    font-family: Monserrat;
    font-style: medium;
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 4%;
    color: var(--text-color_dark);
    margin-right: auto;
    margin-bottom: 32px;
}

.contacts__name {
    width: 138px;
    height: 28px;
    background: var(--accent-color);
    border-radius: 6px;
    text-align: center;
    font-family: Monserrat;
    font-style: medium;
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 4%;
    color: var(--text-color_dark);
    margin-right: auto;
    margin-bottom: 32px;
}

.contacts__city {
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 4%;
    margin-bottom: 8px;
}

.contacts__link {
    font-size: 16px;
    line-height: 1.8;
    color: var(--text-color_address);
}

.contacts__link:hover,
.contacts__link:focus {
    text-decoration: underline;
    color: var(--text-color_address);
 }

.contacts__link::after {
     content: "";
    display: inline-block;
     width: 288px;
     border: 1px solid #E1E1E1;
    // bottom: 32px;
 }

.contacts-page__day {
    margin-top: 32px;
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 4%;

 }

.contacts-page__time {
    font-size: 16px;
    line-height: 1.9;
    color: #ABABAB;

}

.contacts-page__weekend {
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 4%;
    margin-top: 12px;

}

.contacts-page::after {
    content: "";
    display: inline-block;
    width: 288px;
    border: 1px solid #E1E1E1;

}

.contact-social {
    margin-top: 32px;
}

.contact-social__mail {
    font-size: 16px;
    line-height: 1.8;
    color: var(--text-color_dark);
}

.contact-social__tel {
    font-size: 16px;
    line-height: 1.8;
    color: var(--text-color_dark);

}