.footer_bg {
    background-color: var(--base-bg-color);
    width: 100%;
}

.footer_name-first {
font-family: var(--primary-font);
font-weight: 700;
font-size: 18px;
line-height: 1.22;
text-transform: uppercase;
color: var(--text-color_light);
margin-top: 39px;
width: 232px;
}

.footer_name-second {
font-family: var(--primary-font);
font-weight: 700;
font-size: 34px;
line-height: 1.2;
letter-spacing: 0.04em;
text-transform: uppercase;
color: var(--text-color_light);
width: 200px;
margin-top: 16px;
}

.footer_social-list {
    display: flex;
  //  justify-content: right;
   align-items: center;
   column-gap: 27px;
   padding-top: 42px;
   padding-bottom: 38px;
   width: 152px;
   height: 35px;
   margin-left: auto;
}

.footer_social-list-item {
border: none;
}

.footer_social-link {
  display: flex;
  justify-content: center;
  align-items: center;
   fill: #84A178;;
   border-radius: 50%;
   width: 34px;
   height: 34px;
   background-color: #F8D24E;
   border-color: tomato;
   }

  //  .footer_social-link .footer_icon-box:hover {
  //    fill: #FCFCFC;
  //    width: 32px;
  //   height: 32px;
  //   border: none;
  //  }

   .footer_social-link:focus{
    fill:#FCFCFC;
    width: 50px;
   height: 50px;
   outline: none;
   column-gap: 10px;
  }

   .footer_social-link:hover {
     fill:#FCFCFC;
     width: 50px;
    height: 50px;
    outline: none;
    column-gap: 10px;
   }

  //   .footer_social-link.footer_icon-box:focus {
  //   fill: #FCFCFC;
  //   width: 38px;
  //  height: 38px;
  //  border: none;
  // }

.footer_icon-box { 
width: 20px;
height: 20px;
}

.footer_box {
   margin-bottom: 40px;
}

 .footer_contacts {
font-family: var(--primary-font);
font-weight: 700;
font-size: 12px;
line-height: 186%;
text-align: right;
color: #131313;
 }

.footer_phone-color {
    color: var(--text-color_light);
    border: transparent;

    &:focus{
   outline: none;
  }
}

.footer_circulation {
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--primary-font);
    font-weight: 700;
    font-size: 14px;
    line-height: 152%;
    text-align: center;
    color: #7A7A7A;  
    padding-top: 16px;
    padding-bottom: 62px;
    width: 100%;
    margin: 0 auto;
    border-top: 1px solid #E1E1E1;
}

  @media screen and (min-width: 480px) {
   
  }
  
  @media screen and (min-width: 768px) {
    .footer_name-first {
      font-size: 26px;
      line-height: 1.23;
      letter-spacing: 0.04em;
      margin-top: 40px;
      width: 352px;
      }

      .footer_name-second {
        font-size: 48px;
        line-height: 1.23;
        letter-spacing: 0.06em;
        width: 340px;
        margin-top: 9px;
        }
       
        .footer_social-list {
         width: 162px;
      }

        .footer_contacts {
          font-size: 14px;
          margin-bottom: 24px;
         }

        .footer_circulation {
         line-height: 186%;
          letter-spacing: 0.02em;
          padding-top: 14px;
          padding-bottom: 13px;
          width: 100%;
      }

      .footer_box {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 32px;
      }
  }
  
  @media screen and (min-width: 1200px) {
    .footer_name-first {
      font-weight: 400;
      font-size: 30px;
      margin-top: 120px;
      width: 504px;
      }
      .footer_name-second {
        font-size: 58px;
        width: 497px;
        margin-top: 14px;
        }
        .footer_box {
        margin-bottom: 54px;
        }
        
        .footer_contacts {
          font-size: 16px;
          letter-spacing: 0.02em;
          margin-bottom: 6px;
         }

        .footer_circulation {
          margin: 0 auto;
          font-size: 12px;
           padding-top: 12px;
           padding-bottom: 11px;
           width: 1167px;
       }
  }